<template>
  <div class="hello">
    <div class="left-warpper">
      <div class="next-warpper">
        <div v-for="(items,indexs) in nextView" :key="indexs" class="unit-row">
          <div v-for="(item,index) in items" :key="index" class="unit-col" :style="{background:item.bgcolor}">
          </div>
          <br/>
        </div>
      </div>
      <div class="score-wrapper">
        分数 {{score}}
        <!-- 层数 10 -->
      </div>
    </div>
    
    <div class="main-warpper">
      <div v-for="(items,indexs) in backgroundView" :key="indexs" class="unit-row">
        <div v-for="(item,index) in items" :key="index" class="unit-col" :style="{background:item.bgcolor}">
        </div>
        <br/>
      </div>
    </div>
    <div class="button-warpper">
      <button @click="moveCircle()">旋转</button>
      <br/>
      <button @click="moveLeft()">左</button>
      <button @click="moveRight()">右</button> 
      <br/>
      <button @click="moveDown()">下</button>     
      
    </div>
    <div v-if="overView" class="game-over">
      <div>
        Game<br/>
        Over
      </div> 
      <button @click="init()">重新开始</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data() {
    return {
      currentGraph:{
        graph:[],
        color:''
      },
      nextGraph:{
        graph:[],
        color:''
      },
      score:0,
      overView:false,
      //最高层
      topLevel:0,
      nextView:Array(4).fill(0).map(() => Array(4).fill(0).map(() =>{return {
        bgcolor:"#eee",
        fillBoolean:false
      }})),
      backgroundView:Array(20).fill(0).map(() => Array(10).fill(0).map(() =>{return {
        bgcolor:"#eee",
        fillBoolean:false
      }}))
,
      graphList:[
        [[0,3],[0,4],[1,4],[1,5]],
        [[0,4],[0,5],[1,4],[1,3]],
        [[0,4],[0,5],[1,4],[1,5]],
        [[0,4],[1,4],[2,4],[3,4]],       
        [[0,4],[1,4],[2,4],[2,5]],
        [[0,5],[1,5],[2,5],[2,4]],
        [[0,4],[1,3],[1,4],[1,5]],
      ],
      colorList:['#f58f98','#faa755','#ffd400','#45b97c','#94d6da','#2585a6','#8552a1']
    }
  },
  methods:{
    //初始化
    init(){
      this.overView = false
      this.nextGraph = this.randomGraph()
      this.loopInit()
    },
    //循环
    loopInit(){
      this.currentGraph = {...this.nextGraph}
      this.nextGraph = this.randomGraph()
      if(this.positionJudging(this.currentGraph.graph)){
        this.creatGraph(this.currentGraph)
        this.dropGraph()
      }else{
        this.overView = true
      }
      this.nextWarpper()
    },
    //左移
    moveLeft(){
      this.clearGraph(this.currentGraph.graph) 
      let temp = this.currentGraph.graph.map((item)=>[item[0],item[1]-1])
      if(this.positionJudging(temp)){
        this.currentGraph.graph = temp.slice(0)
        this.creatGraph(this.currentGraph)
      }else{
        this.creatGraph(this.currentGraph)
      }  
    },
    //右移
    moveRight(){
      this.clearGraph(this.currentGraph.graph) 
      let temp = this.currentGraph.graph.map((item)=>[item[0],item[1]+1])
      if(this.positionJudging(temp)){
        this.currentGraph.graph = temp.slice(0)
        this.creatGraph(this.currentGraph)
      }else{
        this.creatGraph(this.currentGraph)
      } 
    },
    //下移
    moveDown(){
      this.clearGraph(this.currentGraph.graph) 
      let temp = this.currentGraph.graph.map((item)=>[item[0]+1,item[1]])
      if(this.positionJudging(temp)){
        this.currentGraph.graph = temp.slice(0)
        this.creatGraph(this.currentGraph)
      }else{
        this.creatGraph(this.currentGraph)
      }  
    },
    //判断结束
    gameOver(){
      return this.backgroundView[0].every((item)=>{
        return item.bgcolor === '#eee'
      })
    },
    //判断消除
    fullRemove(){
      this.currentGraph.graph.forEach((item)=>{
        let temp = this.backgroundView[item[0]].every((val)=>{
          let bool = val.bgcolor !== '#eee'
          return bool
        })
        if(temp){
          this.backgroundView[item[0]].every((val)=>{
          return val.bgcolor = '#fff'
        })

        this.backgroundView.splice(item[0],1)
        this.backgroundView.unshift(Array(10).fill(0).map(() =>{return {
          bgcolor:"#eee",
          fillBoolean:false
        }}))
        this.score += 10

        }
      })
      
    },
    //记分

    //旋转
    moveCircle(){
      this.clearGraph(this.currentGraph.graph) 
      let graph = this.currentGraph.graph
      let temp = graph.map((item)=>{
        let x = graph[1][1]+graph[1][0]-item[0]
        let y = graph[1][0]-graph[1][1]+item[1]
        return [y,x]
      })
      if(this.positionJudging(temp)){
        this.currentGraph.graph = temp.slice(0)
        this.creatGraph(this.currentGraph)
      }else{
        this.creatGraph(this.currentGraph)
      } 
    },
    //绘制图形
    creatGraph(vaule){  
      vaule.graph.forEach((item)=>{
        this.backgroundView[item[0]][item[1]].bgcolor = vaule.color
      })
    },
    //清除图形
    clearGraph(vaule){
      vaule.forEach((item)=>{
        this.backgroundView[item[0]][item[1]].bgcolor = "#eee"
      })
    },

    
    //随机图形
    randomGraph(){
      let gra =  {
        graph : this.graphList[parseInt(Math.random()*7)],
        color : this.colorList[parseInt(Math.random()*7)]
      }
      return gra
    },
    //加载下一个
    nextWarpper(){
      this.currentGraph.graph.forEach((item)=>{
        this.nextView[item[0]][item[1]-3].bgcolor = '#eee'
      })
      this.nextGraph.graph.forEach((item)=>{
        this.nextView[item[0]][item[1]-3].bgcolor = this.nextGraph.color
      })
    },
    //判断是否可以移动或旋转或下落
    positionJudging(arr){
      let positionResult = arr.every((val)=>{
        //是否越界
        let overflow = val[0] >= 0 && val[0] <= 19 && val[1] >= 0 && val[1] <= 9
        if(!overflow){
          return false
        }
        //是否有其他元素
        let colorJudging = this.backgroundView[val[0]][val[1]].bgcolor === "#eee"
        return overflow && colorJudging ? true : false
        
      })
      return positionResult
    },
    //下落
    dropGraph(){
      const dropi = setInterval(()=>{
        let temp = this.currentGraph.graph.map((item)=>[item[0]+1,item[1]]) 
        this.clearGraph(this.currentGraph.graph) 
        if(this.positionJudging(temp)){ 
          this.currentGraph.graph = temp.slice(0)
          this.creatGraph(this.currentGraph)
        }else{
          this.creatGraph(this.currentGraph)
          //关闭定时器
          clearInterval(dropi)
          //判断是否可消除
          this.fullRemove()
          //进行下一次循环
          this.loopInit()
        }
      },1000)
    }
  },
  mounted() {
    this.init()
  },
}
</script>

